.updateProfileContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}
.updateProfileBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}
.updateProfileHeading{
    font: 400 1.3vmax 'Roboto';
    color: rgba(0, 0, 0, 0.664);
    text-align: center;
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0,0,0,0.205);
    width: 50%;
    margin: auto;
}
.updateProfileForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    justify-content: space-evenly;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;
}
.updateProfileForm > div {
    display: flex;
    width: 100%;
    align-items: center;
}
.updateProfileForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax "Roboto";
    outline: none;
}
.updateProfileForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}
.updateProfileBtn {
    background-color: tomato;
    color: white;
    width: 100%;
    padding: 0.8vmax;
    font: 300 0.9vmx "Roboto";
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.5s;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}
#updateProfileImage > img {
    width: 3vmax;
    border-radius: 100%;
    margin: 0.5vmax;
}
#updateProfileImage > input {
    display: flex;
    padding: 0;
}
#updateProfileImage > input::file-selector-button {
    cursor: pointer;
    width: 100%;
    height: 5vh;
    margin: 0%;
    border: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0, 0, 0, 0.623);
    transition: all 0.5s;
    z-index: 2;
    padding: 0 1vmax;
    background-color: rgb(255, 255, 255);
}
#updateProfileImage > input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}
.updateProfileBtn:hover {
    background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
    .updateProfileContainer {
      background-color: white;
    }
    .updateProfileBox {
      width: 100vw;
      height: 95vh;
    }
    .updateProfileForm {
      padding: 5vmax;
    }

    .updateProfileForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax "Roboto";
    }
  
    .updateProfileForm > div > svg {
      font-size: 2.8vmax;
    }
  
    #updateProfileImage > img {
      width: 8vmax;
      border-radius: 100%;
    }
  
    #updateProfileImage > input::file-selector-button {
      height: 7vh;
      font: 400 1.8vmax "Roboto";
    }
    .updateProfileBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }
